.SuggestedList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 25%;
  border-right: 1px dashed silver;
}

.SuggestedList h2 {
  height: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.SuggestedListItem__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.SuggestedListItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin: 10px;
  /* padding: 10px; */
  width: 90%;
  margin: 10px auto;
  height: 90px;
}

.SuggestedListItem img {
  height: 60px;
  border-radius: 50%;
  border-bottom: 5px solid purple;
  border-left: 5px solid blueviolet;
  border-right: 5px solid plum;
  border-top: 5px solid plum;
}

.SuggestedListItem__details {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-positive: 2;
      flex-grow: 2;
  padding: 0 10px;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
}

.SuggestedListItem__button {
  height: 50px;
  width: 50px;
  border: none;
  color: silver;
  font-size: 25px;
  cursor: pointer;
}

@media (max-width: 1170px) {
  .SuggestedList {
    width: 35%;
  }
}

@media (max-width: 800px) {
  .SuggestedList {
    border-right: none;
    border-bottom: 1px dashed silver;
    width: 90%;
    -ms-flex-align: center;
        align-items: center;
    margin: 20px auto;
  }
}



